import React, { useState } from 'react'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario.svg';

import { LoginFormContainer, LoginForm, LoginTitle, LoginFormLabel, LoginInput, RememberPasswordContainer, DiscountAnnouncement, DiscounntTextBold,
        LoginFormLabelCheckbox, LoginFormCheckbox, LoginFormBtn, LoginFormForgotPasswordContainer, LoginFormForgotPassword, DiscounntText, LoginInfoIconContainer } from '../../styles/Home'

function Login() {

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [rememberPassword, setRememberPassword] = useState(false)

    const handleUsername = e => { setUsername(e.target.value)}
    const handlePassword = e => setPassword(e.target.value)
    const handleCheckbox = () => setRememberPassword(!rememberPassword)

    return (
        <LoginFormContainer>
            <LoginForm>
                <LoginInfoIconContainer marginLeft="1.5vw">
                    <UserIcon />
                </LoginInfoIconContainer>
                <LoginTitle>INICIO DE SESIÃ“N</LoginTitle>
                <LoginFormLabel>USUARIO</LoginFormLabel>
                <LoginInput 
                    onChange={handleUsername}
                    value={username}
                />
                <LoginFormLabel>CONTRASEÃ‘A</LoginFormLabel>
                <LoginInput 
                    onChange={handlePassword}
                    value={password}
                    type="password"
                />
                <RememberPasswordContainer>
                    <LoginFormLabelCheckbox>Recordar contraseÃ±a</LoginFormLabelCheckbox>
                    <LoginFormCheckbox 
                        type="checkbox"
                        value={rememberPassword}
                        onChange={handleCheckbox}
                    />
                </RememberPasswordContainer>
                <LoginFormBtn>ACEPTAR</LoginFormBtn>
                <LoginFormForgotPasswordContainer>
                    <LoginFormForgotPassword href="">Recuperar contraseÃ±a</LoginFormForgotPassword>
                    <LoginFormForgotPassword href="">Crear nueva cuenta</LoginFormForgotPassword>
                </LoginFormForgotPasswordContainer>
            </LoginForm>
            <DiscountAnnouncement>
                <DiscounntTextBold>15% DESCUENTO</DiscounntTextBold>
                <DiscounntText>EN CUALQUIER SERVICO</DiscounntText>
                <DiscounntTextBold>PAGANDO AQUÃ</DiscounntTextBold>
            </DiscountAnnouncement>
        </LoginFormContainer>
    )
}

export default Login
